.toggle-wrapper {
    margin: 0.5em auto;
  }
  
  .toggle-wrapper label {
    padding: 0.3em;
    font-size: 17px;
    display: block;
  }
  
  .toggleContainer {
    width: 100%;
    position: relative;
    background: #ffffff;
    border: 1px solid #eeeeee;
    border-left: 0;
    border-right: 0;
  }
  
  .toggleContainer .toggleOption {
    display: inline-block;
    position: relative;
    z-index: 1;
    text-align: center;
    height: 40px;
    line-height: 40px;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    color: #807b77;
    font-size: 1em;
  }
  
  .toggleContainer .toggleOption.selected {
    color: #ffffff;
    cursor: initial;
  }

  .myClass {
    background: #939493 !important;
  }
  
  .toggleContainer .toggle {
    position: absolute;
    height: 100%;
    bottom: 0;
    left: 0;
    transition: all 0.4s ease-in-out;
    background: #18922c;
  }
  
  .optionDisabled {
    background: #e6e6e6;
    cursor: no-drop !important;
  }

  .card {
    background: #fff;
    border-radius: 2px;
    position: relative;
  }

  .card-3 {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  }